import React, {Component,Fragment} from 'react';
import SelectedVideo from './Components/SelectedVideo/selectedVideo';
import VideoList from './Components/SelectedVideo/videoList';
import StyledVideoList from './Components/SelectedVideo/StyledVideoList';
import SearchComponent from './Components/Search/search';
import {getTrendingVideos} from './store/actions/AllAction';
import {connect} from 'react-redux';
import { Header, Icon } from 'semantic-ui-react';
import styled from 'styled-components';
import {BrowserRouter,Route,Switch} from 'react-router-dom';
const HeaderContainer=styled.div`
padding:2% 2% 0% 8%
`;
class App extends Component{  
  constructor(props) {
    super(props)     
  } 
  async componentDidMount(){
    this.props.getTrendingVideos();
  }
handleGoBack=()=>{ 
  alert("Shut the fuck up!");
}
  render(){
    return <div>
  <HeaderContainer>
  <Header as='h1' color="grey">
    <Icon link name='caret square right' color="red" onClick={this.handleGoBack}/>
    <Header.Content>My YouTube</Header.Content>
  </Header>
  </HeaderContainer>
  <SearchComponent/>
  <BrowserRouter>
  <Switch>
  <Route exact path="/" render={props=><VideoList/>}/>
  <Route exact path="/select" render={props=><Fragment><SelectedVideo/><StyledVideoList/></Fragment>}/>
  </Switch>  
  </BrowserRouter>    
    </div>
  }
}
const mapDispatchToProps=(dispatch)=>{
  return{
    getTrendingVideos:()=>dispatch(getTrendingVideos())
  }
}
export default connect(null,mapDispatchToProps)(App);
